header {
    display: flex;
    flex-direction: row;
    background-color: white;
    margin-bottom: 1rem;
  }
  
  .logo img {
    height: 5rem;
    width: 11.3rem;
  }
  
  .navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 0.8rem;
    width: -webkit-fill-available;
  }
  
  .navbar a,
  ul {
    list-style-type: none;
    text-decoration: none;
  }
  
  .nav-links-horizontal {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .links-horizontal {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  .icon {
    display: none;
  }
  .nav-links-horizontal .links-horizontal a {
    list-style-type: none;
    text-decoration: none;
    color: #49516f;
    font-weight: 700;
  }
  
  .nav-links-horizontal .links-horizontal li {
    margin: 1rem;
    padding-right: 1rem;
    padding-left: 1rem;
  }
  .home-span {
    color: #2AA7FF;
  }
  .logo{
      font-size: 3rem;
  
  }
  .logo .medi{
      color:#2AA7FF
  }
  
  
  a .login-span {
    background-color: #2AA7FF;
    color: #ffffff;
    width: 6rem;
    height: 2.4rem;
    filter: drop-shadow(0px 2px 2px rgba(72, 135, 230, 0.5));
    text-align: center;
    padding-top: 0.5rem;
    border-radius: 8px;
    padding: auto;
  }
  
  @media screen and (max-width: 900px) {
    .navbar {
      display: block;
      width: -webkit-fill-available;
    }
    .navbar .logo {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-around;
      align-items: center;
    }
  
    .nav-links-horizontal {
      display: none;
    }
    .responsive {
      display: block;
      transition: display ease-in 2s;
    }
  
    .links-horizontal {
      display: block;
      text-align: center;
    }
    .links-horizontal .login-span {
      margin: auto;
    }
    .navbar .icon {
      display: block;
      float: right;
    }
    .nav-links-horizontal .links-horizontal li {
      margin: 1rem;
      padding-right: 1rem;
      padding-left: 1rem;
      font-size: 1.5rem;
    }
    a .login-span {
      background-color: #2AA7FF;
      color: #ffffff;
      width: 6rem;
      height: 2.4rem;
      filter: drop-shadow(0px 19px 16px rgba(72, 135, 230, 0.5));
      text-align: center;
      padding-top: 0rem;
      border-radius: 8px;
      padding: auto;
    }
  }
  
  
  .show-modal {
    display: block;
  }
  
  .underline {
    border-bottom: 2px solid #ff477e;
  }